import React, { Component } from 'react';
import { Link, Route, Switch, withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import Delivery from '../delivery';
import Shipping from '../shipping';
import Header from '../common/Header';
import Sidenav from '../common/Sidenav';
import './style.scss';

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pageTitle: 'Dashboard',
      sidenavVisibility: false
    };
    this.setHeader = this.setHeader.bind(this);
    this.toggleSidenav = this.toggleSidenav.bind(this);
  }

  setHeader(pageTitle) {
    this.setState({ pageTitle });
  }

  toggleSidenav() {
    this.setState({ sidenavVisibility: !this.state.sidenavVisibility });
  }

  render() {
    const { location } = this.props;
    return (
      <section className="section-wrapper dashboard">
        <Sidenav
          visible={this.state.sidenavVisibility}
          toggleSidenav={this.toggleSidenav}
        />
        <Header
          title={this.state.pageTitle}
          toggleSidenav={this.toggleSidenav}
        />
        <div className="dashboard-modules">
          <Link to="/delivery" className="modules">
            <i className="fab fa-dropbox" />
            Delivery
          </Link>
          <Link to="/" className="modules disabled">
            <i className="fas fa-arrows-alt" />Colli
          </Link>
          <Link to="/" title="Pallete" className="modules disabled">
            <i className="fas fa-cubes" />
            <span> Pallete </span>
          </Link>
          <Link title="Shipping" to="/shipping" className="modules">
            <i className="fas fa-truck" />
            <span> Shipping </span>
          </Link>

          <div>
            <Switch location={location}>
              <Route
                path="/shipping"
                render={() => <Shipping setHeader={this.setHeader} />}
              />
              <Route
                path="/delivery"
                render={() => <Delivery setHeader={this.setHeader} />}
              />
            </Switch>
          </div>
        </div>
      </section>
    );
  }
}

Dashboard.propTypes = {
  location: PropTypes.object.isRequired // eslint-disable-line react/forbid-prop-types
};

export default withRouter(Dashboard);



