import React, { Component } from 'react';
import { Link, Switch, Route } from 'react-router-dom';
import Picking from './components/picking';
import Packing from './components/packing';

import "./style.scss";

class Delivery extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pageTitle: 'Delivery'
    };
    this.pageTitle = 'Delivery';
    this.setHeader = this.setHeader.bind(this);
  }
  componentDidMount() {
    this.props.setHeader(this.pageTitle);
  }

  setHeader(pageTitle) {
    this.pageTitle = pageTitle;
  }
  render() {
    console.log('delivery rendering');
    return (
      <section className="section-wrapper delivery">
        <h2>Delivery List </h2>
        
        <p>
          It is a long established fact that a reader will be distracted by the
          readable content of a page when looking at its layout. The point of
          using Lorem Ipsum is that it has a more-or-less normal distribution of
          letters, as opposed to using 'Content here, content here', making it
          look like readable English. 
        </p>
        <Link to={`/delivery/picking`}>picking </Link>
        <p>
          Many desktop publishing packages and web
          page editors now use Lorem Ipsum as their default model text, and a
          search for 'lorem ipsum' will uncover many web sites still in their
          infancy. Various versions have evolved over the years, sometimes by
          accident, sometimes on purpose (injected humour and the like).
        </p>
        <Link to={`/delivery/packing`}>Packing </Link>

        <Route
          path={`/delivery/picking`}
          render={() => <Picking setHeader={this.setHeader} />}
        />
        <Route
          path={`/delivery/packing`}
          render={() => <Packing setHeader={this.setHeader} />}
        />
      </section>
    );
  }
}

export default Delivery;



